<template>
  <div id='app'>
    <div class='container-fluid' v-show="answerShow==false">
      <Header img="" school='Go语言实战抽奖系统'></Header>
      <div class='row tap'>
        <HorizontalTap class='center' :data='data2' :active="0" style="width:1088px;"></HorizontalTap>
      </div>
      <div class="row">
        <div class="center-block">
          <div class="left">
            <div class="bar">
              <span>开发阶段和任务</span>
              <span class="second">开发任务详情</span>
            </div>
            <div class="main">
              <div class="main-left" v-if="stepData">
                <TeamStepList @activeItem="activeItemList" :data="stepData" :expand-id=currentStageId :active-id=currentStepId></TeamStepList>
              </div>
              <div class="main-right">
                <div v-if="stepInfo && stepInfo.category === 0">
                  <div class="stepInfoTitle" style="margin-top: 0px!important;">任务说明</div>
                  <div class="stepInfoContent mt-3">
<!--                    <i class="iconfont iconrenwumingcheng mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color:inherit">任务名称：</span>
                    <span style="color:#666666">{{stepInfo.stepName}}</span>
                  </div>
                  <div class="mt-3 stepInfoContent">
<!--                    <i class="iconfont iconrenwumiaoshu mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color:inherit">任务描述：</span>
                    <span style="color:#666666">
                      {{stepInfo.description}}
                    </span>
                  </div>
                  <div class="mt-3 stepInfoContent">
<!--                    <i class="iconfont iconrenwuyaoqiu mr-2"></i>-->
<!--                    <i class="iconfont iconrenwuyaoqiu mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务目的：</span>
                    <span style="color:#666666" v-if="stepInfo.purpose">
                      {{stepInfo.purpose}}
                    </span>
                    <span style="color:#666666" v-else>
                     无
                    </span>
                  </div>
                  <div class="mt-3 stepInfoContent">
<!--                    <i class="iconfont iconrenwuyaoqiu mr-2"></i>-->
<!--                    <i class="iconfont iconrenwuyaoqiu mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务要求：</span>
                    <span style="color:#666666" v-if="stepInfo.required">
                      {{stepInfo.required}}
                    </span>
                    <span style="color:#666666" v-else>
                     无
                    </span>
                  </div>
                  <div class="stepInfoTitle" v-show="stepInfo.video || stepInfo.templateName">任务相关资料</div>
                  <div class="mt-3 stepInfoContent" v-show="stepInfo.video">
<!--                    <i class="iconfont iconshipinjiaocheng mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">视频教程：</span>
                    <span class="ml-2" style="color:#666666;cursor:pointer;" @click="playVideo(stepInfo.video)">
                    <img src='../../../../assets/img/university.png'>
                    </span>
<!--                    <i class="el-icon-video-play" style="color: #6612cf"></i>-->
                  </div>
                  <div class="mt-3 stepInfoContent" v-show="stepInfo.templateName">
<!--                    <i class="iconfont iconwendangmoban mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">文档模板：</span>
                    <div v-for="(obj,index) in templateName" v-bind:key="index" :style="index === 0 ? 'margin-left: 65px; margin-top:-20px;':'margin-left: 65px;'">
                      <span class="ml-2" style="color:#6715CE;cursor:pointer;" @click="downloadTemplate(obj)">{{obj.split('$_$')[1]}}</span>
                      <i class="iconfont iconxiazai" style="color:#6715CE;margin-left: 5px;"></i>
                    </div>
                  </div>

                  <div class="stepInfoTitle">成果要求及上传</div>
                  <div class="mt-3 stepInfoContent">
<!--                    <i class="iconfont iconchengguoyaoqiu mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">成果要求：</span>
                    <span class="ml-2" style="color:#666666">上传规格为word或zip文件，每次只能上传一个文件</span>
<!--                    <span class="ml-2" style="color:#666666">{{stepInfo.outputResult}}</span>-->
                  </div>
                  <div class="row mt-3 stepInfoContent">
<!--                    <i class="iconfont iconchengguoshangchuan mr-2"></i>-->
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">成果上传：</span>
                    <span slot="tip" class="ml-2 el-upload__tip" style="font-size: 14px">{{fileName}}</span>
                    <el-upload class= "upload-demo" ref= "upload" action= "upLoad" style="float: right;"
                      :limit= "1"
                      :before-upload= "beforeUpload">
                      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
<!--                      <a href="./static/moban.xlsx" rel="external nofollow" download="模板"><el-button size="small" type="success">下载模板</el-button></a>-->
                      <!-- <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button> -->
                    </el-upload>
<!--                    <span slot="tip" class="el-upload-list__item-name">{{fileName}}</span>-->
<!--                    <p class="ml-2" style="color:#666666">上传规格为word或zip文件，每次只能上传一个文件</p>-->
                  </div>
<!--                  <div class="complete" v-show="stepInfo.status !== 1">-->
<!--                    <i class="iconfont iconduihao"></i>-->
<!--                    <span v-if="stepInfo.status === -1">步骤未开始</span>-->
<!--&lt;!&ndash;                    <span v-if="stepInfo.status === 0">步骤未完成</span>&ndash;&gt;-->
<!--                    <span v-if="stepInfo.status === 0">步骤未指派</span>-->
<!--                    <span v-if="stepInfo.status === 3">步骤已截止</span>-->
<!--                    <span v-if="stepInfo.status === 2">步骤已完成</span>-->
<!--                  </div>-->
                  <div class="mt-15 text-center" style="margin-top: 30px;" v-show="stepInfo.status === 1">
                    <el-button type="primary" @click="upLoad">提交</el-button>
                  </div>
                  <div class="mt-15 text-center" style="margin-top: 30px;" v-show="stepInfo.status === 2 || stepInfo.status === 3">
                    <el-button type="primary" @click="seeRightAnswer(stepInfo.answer)">查看标准答案</el-button>
                  </div>
                </div>
                <div v-if="stepInfo && stepInfo.category === 1" style="text-align: center;">
                  <div class="testBox">
                    <p style="text-align: left">{{stepInfo.description}}</p>
                    <p style="text-align: left">{{stepInfo.required}}</p>
                    <div v-show="stepInfo.status === 2" id="myChart" style="width:300px;height:300px"></div>

                    <span v-show="stepInfo.status === 2" style="font-size: 20px">当前排名：{{orderNum}}</span>
                  </div>
                  <div class="mt-3 text-center" v-show="stepInfo.status === 1">
                    <el-button type="primary" @click="answerPage">调查评测</el-button>
                  </div>
<!--                  <div class="mt-3 text-center" v-show="stepInfo.status === 2 || stepInfo.status === 3">-->
<!--                    <el-button type="primary" @click="seeAnswerPageResult">查看测评结果</el-button>-->
<!--                  </div>-->
                </div>
                <div v-if="stepInfo && stepInfo.category === 2">
                  <div>
                    <i class="iconfont iconrenwumingcheng mr-2"></i>
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务名称：</span>
                    <span>编码与调试</span>
                  </div>
                  <div class="mt-3">
                    <i class="iconfont iconrenwumiaoshu mr-2"></i>
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务描述：</span>
                    <p class="ml-4">编码人员依据《详细设计报告》的内容，遵循编码规范的要求对设计元素进行编码实现和调试</p>
                  </div>
                  <div class="mt-3">
                    <i class="iconfont iconrenwuyaoqiu mr-2"></i>
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务要求：</span>
<!--                    <p class="ml-4">按照详细设计要求完成编码、调试工作。调试代码中的调试代码应在调试后删除</p>-->
                    <p class="ml-4"> {{stepInfo.description}}</p>
                  </div>
                  <div class="mt-3">
                    <i class="iconfont iconshipinjiaocheng mr-2"></i>
                    <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">视频教程：</span>
                    <h5>编程联系</h5>
                    <ul style="list-style-type:decimal;">
                      <li style="color:green;"><span>全局样式</span><i class="el-icon-circle-check ml-3"></i></li>
                      <li style="color:green;"><span>强调内容</span><i class="el-icon-circle-check ml-3"></i></li>
                      <li><span>无序列表、有序列表</span><i class="el-icon-circle-check ml-3"></i></li>
                      <li><span>弹出框--弹出框增加过渡动画效果</span><i class="el-icon-circle-check ml-3"></i></li>
                    </ul>
                  </div>
                  <div class="mt-3 text-center">
                    <el-button type="primary">去编程</el-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="right">
            <div class="bar">
              <HorizontalTap @activeItem="activeItem" :data=data3 :active="0" ></HorizontalTap>
            </div>
            <div class="main" v-show="showTapName === '成果物点评'">
<!--              有点评数据-->
              <div class="box" v-show="isReviewData==true" v-for="obj in commentData" v-bind:key="obj.outputId" style="margin-top: 5px">
<!--                待修改-->
                <div class="doc-box">
                  <i class="iconfont iconwenhao iconfontForWhy" v-show="obj.status==2"></i>
                  <i class="iconfont iconyiwancheng" v-show="obj.status==1"></i>
                  <i class="iconfont iconweiwancheng iconfontForNo" v-show="obj.status==-1||obj.status  ==0"></i>
                  <div class="photo"></div>
<!--                  <div class="name">文档名称：《软件需求调研提纲1》.docx</div>-->
                  <div class="name">{{obj.name}}</div>
                  <div class="time">{{obj.uploadDate}} </div>
                  <div class="stateForWhy" v-show="obj.status==2">需修改</div>
                  <div class="state" v-show="obj.status==1">已通过</div>
                  <div class="stateForNo" v-show="obj.status==-1">未通过</div>
                  <div class="stateForNo" v-show="obj.status==0">未点评</div>
                  <a @click="showCommentDetailed(obj.outputId)" v-show="obj.status!=0">
                    <span v-show="obj.commentCode=='REPEAT_CHECK'">文档查重</span>
                    <span v-show="obj.commentCode=='CODE_STYLE'">代码规范检查</span>
                    <span v-show="obj.commentCode=='DOC_FORMAT'">文档格式检查</span>
                    <span v-show="obj.commentCode=='ASSISTANT_COMMENT'">助教点评</span>
                    <i class="el-icon-caret-bottom"></i></a>
                </div>
                <div class="doc-detail" v-show="obj.commentDetailedIsShow">
<!--                  <a class="link-one">查看系统点评</a>-->
                  <span>成果物缺陷点：</span>
                  <span v-if="obj.evaluates!=''">
                    {{obj.evaluates}}
                  </span>
                  <span v-else>无</span>
                  <span>助教点评：</span>
                  <span>
                    {{obj.message}}
                  </span>
                  <a class="link-two" @click="writeRely" v-show="state==0">编辑回复</a>
                  <div class="disable" v-show="state==0">
                    {{obj.evaluates}}
                  </div>
                  <el-input v-show="isShow"
                    type="textarea"
                    :rows="2"
                    placeholder="请输入内容"
                    v-model="commentContent" style="margin-top: 5px">
                  </el-input>
                  <div class="row footer"  v-show="state==0">
                    <el-upload style="width: 60%; float: left;"
                    :action="doUpload"
                    :data="pppss">
                      <i class="iconfont icontianjiawenjian"></i>
                      <span>重新上传文档</span>
                    </el-upload>
                    <el-button type="primary" @click="submitFont(obj.outputId)">提交</el-button>
                  </div>
                </div>
              </div>
              <div v-show="isReviewData==false">
<!--                <div class="noReviewData"></div>-->
                <div class="noReviewDataFont">暂无任何点评数据</div>
              </div>
            </div>
            <div v-show="showTapName === '指派任务'">
              <div class="row member-list allow-click">
                <div class="box member-box" :class="[obj.status === 1 ? 'active' : '']" v-for="obj in appointListData" v-bind:key="obj.userCode" @click="appointMember(obj)">
                  <i class="iconfont iconyiwancheng" v-if="obj.status === 1"></i>
                  <div class="name">{{obj.userName}}</div>
                  <div class="position">{{obj.groupLeader === 1 ? '队长' : '队员'}}</div>
                  <div class="head-portrait"></div>
                </div>
              </div>
              <div class="row mt20">
                <el-button type="primary" class="center" @click="saveAppoint">保存</el-button>
              </div>
            </div>
            <div v-show="showTapName === '队友进行状态'">
              <div class="row member-list">
                <div class="box member-box" :class="[obj.status === 1 ? 'active' : '']" v-for="obj in memberStatusData" v-bind:key="obj.userCode">
                  <i v-if="obj.status === 1" class="iconfont iconyiwancheng"></i>
                  <div class="name">{{obj.userName}}</div>
                  <div class="position">{{obj.groupLeader === 1 ? '队长' : '队员'}}</div>
                  <div class="step">{{obj.status === 1 ? '已完成' : '未完成'}}</div>
                  <div class="head-portrait"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-show="answerShow == true">
      <Header img="" school='Go语言实战抽奖系统'></Header>
      <div class="mt-15 text-center" style="margin-top: 30px;">
        <div class="paperTitle" v-if="paperInfo">{{paperInfo.paperName}} <p style="color: #6612cf">{{h + ":" +m +":" + s}}</p></div>
        <div class="paperContent" v-for="(obj, index) in paperInfo.problems" v-bind:key="obj.problemId">
          <p class="paperProblem">{{index+1  + '.' +obj.problemContent}}</p>
          <div class="paperOptionContent" v-for="(value, key) in obj.options" v-bind:key="key" @click="answerOptionSet(obj.problemId, key)">
            <el-radio v-model="obj.radio" :label="key">{{key + '. ' +value}}</el-radio>
          </div>
        </div>
      </div>
      <div style="margin-top: 90px;text-align: center;">
        <el-button type="primary" @click="commitPage">提交</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import TeamStepList from '../../../../components/school-training/TeamStepList.vue'
import Vue from 'vue'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    TeamStepList
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      doUpload: '/api/up/file',
      pppss: {
        srid: ''
      },
      winWidth: window.innerWidth,
      data2: [{
        name: '执行任务',
        url: ''
      }, {
        name: '任务单',
        url: 'work-order.html'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html'
      // }, {
      //   name: '竞速排名',
      //   url: ''
      // }, {
      //   name: '励英社区',
      //   url: ''
      }],
      // data3: [{ name: '成果物点评' }, { name: '指派任务' }, { name: '队友进行状态' }],
      data3: [{ name: '成果物点评' }],
      currentStageId: -1,
      currentStepId: -1,
      taskData: [],
      stepData: null,
      commentContent: '',
      isShow: false,
      showTapName: '成果物点评',
      isReviewData: true,
      state: 1,
      appointListData: [],
      isTeamLeader: 0,
      memberStatusData: [],
      stepInfo: null,
      teamId: '',
      message: '',
      evaluates: '',
      commentData: [],
      files: {},
      fileName: '暂未上传文件',
      answerShow: false,
      paperInfo: {},
      paperId: '',
      starttime: '',
      answer: {},
      answerLog: '',
      problemidLog: '',
      timeLimit: 0,
      d: '',
      h: '',
      m: '',
      s: '',
      document: '',
      listTotal: [],
      listResult: [],
      orderNum: '暂无排名',
      templateName: []
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      this.teamId = localStorage.getItem('teamId')
      if (this.teamId === '' || this.teamId === 'undefined' || this.teamId === null) {
        window.location = 'school-main.html'
      } else {
        this.loadStepListData()
      }
    },
    loadStepListData () {
      let me = this
      this.$http.post('/doproject/steplist', {
        'teamid': me.teamId
      }, res => {
        console.log(res)
        if (res.success) {
          for (let obj1 of res.data.stages) {
            obj1.iconShow = false
            let k = 0
            for (let obj2 of obj1.steps) {
              if (obj2.status === 2) {
                k++
              }
              if (obj2.status === 3 || k === obj1.steps.length) {
                obj1.iconShow = true
                break
              }
            }
          }
          me.stepData = res.data.stages
          me.currentStageId = res.data.currentStageId
          for (let obj of me.stepData) {
            if (obj.stageId === me.currentStageId) {
              me.currentStepId = obj.steps[0].stepId
            }
          }
          console.log(me.currentStepId)

          this.loadStepinfo()
          // this.loadAppointListData()
          // this.loadMemberStatusData()
          this.getComment()
        }
      })
    },
    loadAppointListData () {
      let me = this
      this.$http.post('/doproject/appointstatus', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        console.log(res)
        if (res.success) {
          me.appointListData = res.data.status
          me.isTeamLeader = res.data.teamLeader
        }
      })
    },
    loadMemberStatusData () {
      let me = this
      this.$http.post('/doproject/memberstatus', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        console.log(res)
        if (res.success) {
          me.memberStatusData = res.data
        }
      })
    },
    loadStepinfo () {
      let me = this
      console.log(me.teamId)
      console.log(me.currentStepId)
      this.$http.post('/doproject/stepinfo', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        console.log(res)
        if (res.success) {
          me.stepInfo = res.data
          me.data3 = [{ name: '成果物点评' }]
          // if (me.stepInfo.type === 1) {
          //   me.data3 = [{ name: '成果物点评' }, { name: '指派任务' }, { name: '队友进行状态' }]
          // } else {
          //   me.data3 = [{ name: '成果物点评' }, { name: '队友进行状态' }]
          // }
        }
      })
    },
    getComment () {
      let me = this
      this.$http.post('/doproject/outputlist', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        console.log(res)
        if (res.data !== null) {
          me.isReviewData = true
          for (let obj of res.data) {
            obj.commentDetailedIsShow = false
            obj.message = ''
          }
          me.commentData = res.data
        } else {
          me.isReviewData = false
        }
      })
    },
    sizeChange (size) {
      return this.winWidth * size / 1920 + 'px'
    },
    clickStep (data) {
      console.log(data)
    },
    writeRely () {
      let me = this
      me.isShow = !me.isShow
    },
    showCommentDetailed (outputid) {
      let me = this
      console.log(me.commentData)
      console.log(me.commentData[outputid])
      for (let obj1 of me.commentData) {
        if (obj1.outputId === outputid) {
          obj1.commentDetailedIsShow = !obj1.commentDetailedIsShow
        }
      }// me.commentDetailedIsShow = !me.commentDetailedIsShow
      this.$http.post('/doproject/assistantcommentinfo', {
        'outputid': outputid
      }, res => {
        console.log(res)
        if (res.success) {
          for (let obj2 of me.commentData) {
            if (obj2.outputId === outputid) {
              obj2.message = ''
              obj2.evaluates = ''
              for (let obj of res.data.advices) {
                obj2.message += obj
              }
              for (let obj1 of res.data.evaluates) {
                obj2.evaluates += obj1
              }
            }
          }
          // me.evaluates = res.data.evaluates
        }
      })
    },
    activeItem (index, name) {
      let me = this
      me.showTapName = name
      if (index === 0) {
        me.getComment()
      }
    },
    activeItemList (index, name) {
      let me = this
      console.log(me.teamId)
      console.log(index)
      console.log(name)
      me.fileName = '暂未上传文件'
      me.currentStepId = name

      this.getComment()
      this.$http.post('/doproject/stepinfo', {
        'teamid': me.teamId,
        'stepid': name
      }, res => {
        console.log(res)
        if (res.success) {
          this.templateName = []
          if (res.data.templateName.indexOf(',') !== -1) {
            this.templateName = res.data.templateName.split(',')
          } else {
            this.templateName.push(res.data.templateName)
          }
          me.stepInfo = res.data
          me.data3 = [{ name: '成果物点评' }]
          // if (me.stepInfo.type === 1) {
          //   me.data3 = [{ name: '成果物点评' }, { name: '指派任务' }, { name: '队友进行状态' }]
          // } else {
          //   me.data3 = [{ name: '成果物点评' }, { name: '队友进行状态' }]
          // }
        }
        if (res.data.type === 1 && res.data.status === 2) {
          me.$http.post('/doproject/evaluating/evaluatingresultinfo', {
            'teamid': me.teamId,
            'stepid': name
          }, res => {
            console.log(res)
            this.orderNum = res.data.order
            this.listResult = res.data.list2
            this.listTotal = res.data.list1
            this.$nextTick(function () {
              this.document = document.getElementById('myChart')
              this.drawLine()// => 'updated'
            })
          })
        }
      })
    },
    appointMember (obj) {
      obj.status = 1
    },
    saveAppoint () {
      let me = this
      let userCodes = []
      for (let user of this.appointListData) {
        if (user.status === 1) {
          userCodes.push(user.userCode)
        }
      }
      if (userCodes.length > 0) {
        me.$alert('任务指派后将无法修改，是否进行保存', '提示', {
          confirmButtonText: '确定',
          callback: action => {
            me.$http.post('/doproject/appointstep', {
              'teamid': me.teamId,
              'stepid': 1,
              'users': userCodes
            }, res => {
              console.log(res)
              if (res.success) {
                me.$message('保存成功')
              }
            })
          }
        })
      }
    },
    submitFont (outputid) {
      let me = this
      if (me.commentContent === '') {
        me.$alert('请输入需要提问的内容后再提交', '提示', {
          confirmButtonText: '确定'
        })
      } else {
      //   me.$http.post('/doproject/submitfaq', {
      //     'teamid': me.teamId,
      //     'stepid': me.currentStepId,
      //     'message': me.commentContent
      //   }, res => {
      //     console.log(res)
      //     if (res.success) {
      //       me.$message('保存成功')
      //     }
      //   })
        me.$http.post('/doproject/submitfeedback', {
          'outputid': outputid,
          'feedbacks': me.commentContent
        }, res => {
          console.log(res)
          if (res.success) {
            me.$message('保存成功')
          }
        })
        me.upLoad()
      }
    },
    playVideo (video) {
      window.open('' + video, '_blank')
    },
    beforeUpload (file) {
      this.file = file
      this.files = file
      const extension = file.name.split('.')[1] === 'doc'
      const extension1 = file.name.split('.')[1] === 'docx'
      const extension2 = file.name.split('.')[1] === 'zip'
      const isLt2M = file.size / 1024 / 1024 < 5
      if (!extension && !extension1 && !extension2) {
        this.$message.warning('上传模板只能是 doc、docx、zip格式!')
        return
      }
      if (!isLt2M) {
        this.$message.warning('上传模板大小不能超过 5MB!')
        return
      }
      this.fileName = file.name
      return false // 返回false不会自动上传
    },
    upLoad () {
      let me = this
      console.log(me.file)
      console.log({
        'teamid': me.teamId,
        'stepid': me.currentStepId,
        'file': me.file
      })
      var formData = new FormData()
      formData.append('file', me.file)
      formData.append('teamid', me.teamId)
      formData.append('stepid', me.currentStepId)
      console.log(formData)
      this.$http.uploadFile('/doproject/output/upload', formData, res => {
        console.log(res)
        if (res.success) {
          me.$message('上传成果物成功')
        }
      })
    },
    downloadTemplate (name) {
      this.$axios({
        method: 'post',
        url: 'http://39.100.37.131:8085/lying-web/doproject/output/downloadtemplate',
        data: { 'filename': name },
        responseType: 'blob',
        headers: {
          token: localStorage.getItem('token'),
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(
        (response) => {
          var blob = new Blob([response.data])
          var downloadElement = document.createElement('a')
          var href = window.URL.createObjectURL(blob)
          downloadElement.href = href
          downloadElement.download = name
          document.body.appendChild(downloadElement)
          downloadElement.click()
          document.body.removeChild(downloadElement)
          window.URL.revokeObjectURL(href)
          console.log(response)
        }
      ).catch(
        (error) => {
          console.log(error)
        }
      )
    },
    seeRightAnswer (outputName) {
      let me = this
      if (outputName !== null) {
        me.$hideContent()
        me.$http.post('/assistant/openpdf', { outputName: outputName + '_答案.doc' }, res => {
          if (res.success) {
            res.data = JSON.parse(res.data)
            me.$showPDF(res.data.fileUrl)
            // me.pdfUrl = res.data.fileUrl
          }
        })
      } else {
        me.$message('此任务无答案')
      }
    },
    answerPage () {
      let me = this
      me.$http.post('/doproject/evaluating/paperinfo', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        console.log(res)
        if (res.success) {
          for (let obj of res.data.problems) {
            obj.radio = -1
            Vue.set(me.answer, obj.problemId, '')
          }
          me.timeLimit = res.data.timeLimit * 1000 * 60
          this.countTime()
          me.paperInfo = res.data
          me.paperId = res.data.paperId
        }
      })
      let year = new Date(new Date()).getFullYear()
      let month = new Date(new Date()).getMonth() + 1 < 10 ? ' 0 ' + (new Date(new Date()).getMonth() + 1) : new Date(new Date()).getMonth() + 1
      let date = new Date(new Date()).getDate() < 10 ? '0' + new Date(new Date()).getDate() : new Date(new Date()).getDate()
      let hh = new Date(new Date()).getHours() < 10 ? '0' + new Date(new Date()).getHours() : new Date(new Date()).getHours()
      let mm = new Date(new Date()).getMinutes() < 10 ? '0' + new Date(new Date()).getMinutes() : new Date(new Date()).getMinutes()
      let ss = new Date(new Date()).getSeconds() < 10 ? '0' + new Date(new Date()).getSeconds() : new Date(new Date()).getSeconds()
      me.starttime = year + '-' + month + '-' + date + ' ' + hh + ':' + mm + ':' + ss
      me.answerShow = true
    },
    commitPage () {
      let me = this
      console.log(me.starttime)
      console.log(me.answer)
      console.log()
      me.$http.post('/doproject/evaluating/submit', {
        'teamid': me.teamId,
        'stepid': me.currentStepId,
        'paperid': me.paperId,
        'starttime': me.starttime,
        'answer': me.answer
      }, res => {
        console.log(me.starttime)
        console.log(res)
        if (res.success) {
          me.$message('保存成功')
        }
      })
      me.answerShow = false
    },
    answerOptionSet (key, answerOption) {
      console.log(key)
      console.log(answerOption)
      let me = this
      for (let obj of me.paperInfo.problems) {
        if (obj.problemId === key) {
          obj.radio = answerOption
        }
      }
      console.log(me.paperInfo.problems)
      Vue.set(me.answer, key, answerOption)
      // 记录测评日志
      // if (me.problemidLog === key && me.answerLog === answerOption) {
      //   me.$message('请勿重复点击')
      // } else {
      //   me.$http.post('/doproject/evaluating/logsubmit', {
      //     'problemid': key,
      //     'answer': answerOption
      //   }, res => {
      //     if (res.success) {
      //       me.$message('记录日志成功')
      //       me.problemidLog = key
      //       me.answerLog = answerOption
      //     }
      //   })
      // }
    },
    seeAnswerPageResult () {
      this.$message('查看结果')
    },
    countTime: function () {
      if (this.timeLimit >= 0) {
        this.d = Math.floor(this.timeLimit / 1000 / 60 / 60 / 24)
        this.h = Math.floor(this.timeLimit / 1000 / 60 / 60 % 24)
        this.m = Math.floor(this.timeLimit / 1000 / 60 % 60)
        this.s = Math.floor(this.timeLimit / 1000 % 60)
      }
      if (this.timeLimit === 0) {
        this.commitPage()
        this.$message('时间已到')
      }
      this.timeLimit = this.timeLimit - 1000
      setTimeout(this.countTime, 1000)
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      console.log(this.document)
      var myChart = this.$echarts.init(this.document)
      // 绘制图表
      // myChart.setOption({
      //   title: {
      //     text: '在Vue中使用echarts'
      //   },
      //   tooltip: {},
      //   xAxis: {
      //     data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
      //   },
      //   yAxis: {},
      //   series: [{
      //     name: '销量',
      //     type: 'bar',
      //     data: [5, 20, 36, 10, 10, 20]
      //   }]
      // })
      myChart.setOption({
        title: {
          text: '测评结果'
        },
        tooltip: {},
        // legend: {
        //   data: ['预算分配（Allocated Budget）', '实际开销（Actual Spending）']
        // },
        radar: {
          // shape: 'circle',
          name: {
            textStyle: {
              color: '#fff',
              backgroundColor: '#999',
              borderRadius: 3,
              padding: [3, 5]
            }
          },
          indicator: this.listTotal
        },
        series: [{
          // name: '预算 vs 开销（Budget vs spending）',
          type: 'radar',
          // areaStyle: {normal: {}},
          data: this.listResult
          // [
          // {
          //   value: [4300, 10000, 28000, 35000, 50000, 19000],
          //   // name: '预算分配（Allocated Budget）'
          // }
          // ,
          // {
          //   value: [5000, 14000, 28000, 31000, 42000, 21000],
          //   name: '实际开销（Actual Spending）'
          // }
        // ]
        }]
      })
    }
  }
}
</script>
